import React from "react";
import styled from "styled-components";
import BackgroundResource from "../Resources/Images/gameover.png";

const Container = styled.div`
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 400px;
  @media (min-width: 768px) {
    width: 600px;
  }

  animation-duration: 1s;
  animation-name: slidein;

  @keyframes slidein {
    from {
      top: 150%;
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
`;
const BackgroundImage = styled.img`
  width: 100%;
  object-fit: contain;
`;
const Content = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
`;
const GameOverText = styled.div`
  font-weight: 600;
  font-size: 2.8em;
  margin-bottom: 40px;
  @media (min-width: 768px) {
    font-size: 4em;
  }
`;
const ButtonGroup = styled.div`
  display: flex;
  width: 80%;
`;
const Button = styled.div`
  background-image: linear-gradient(#ee609c, #b565d9);
  border-radius: 10px;
  padding: 20px;
  white-space: nowrap;
  color: white;
  font-size: 1em;
  flex: 1;
  margin: 10px;
  text-align: center;
  cursor: pointer;
  @media (min-width: 768px) {
    font-size: 1.5em;
  }
`;

const GameOverModal = (props) => {
  const { isOpened, resetGame, exitGame } = props;
  const onResetClick = (e) => resetGame();
  const onOtherGameClick = (e) => exitGame();
  return (
    <>
      {isOpened && (
        <Container>
          <BackgroundImage src={BackgroundResource}></BackgroundImage>
          <Content>
            <GameOverText>GAME OVER</GameOverText>
            <ButtonGroup>
              <Button onClick={onResetClick}></Button>
              <Button onClick={onOtherGameClick}>  </Button>
            </ButtonGroup>
          </Content>
        </Container>
      )}
    </>
  );
};

export default GameOverModal;
